import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import { post, tokenpost } from '@/api/user.js';
import { api } from '@/config/common.js';
import cuLoading from '@/components/custom/cu-loading.vue';
import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import helangCheckbox from '@/components/helang-checkbox/helang-checkbox.vue';
import { dateFormat, numberFormat } from '@/utils/tools.js';
import action from "@/components/shufei-action/action.vue"
export default {
	components: {
		uniLoadMore,
		uniList,
		uniListItem,
		WucTab,
		action,
		'helang-checkbox': helangCheckbox
	},
	data() {
		return {
			loadmore: 'more',
			loading: false,
			pageIndex: 0,
			pageRows: 15,
			title: '续费/订单',
			pickerIndex: 2,
			TabCur: 0,
			content_show_id: 0,
			dataList: [],
			ordertype: '1,2',
			tabList: [{ name: '续费' }, { name: '我的订单' }],
			productList: [],
			refundList:[],
			nowId:-1
		};
	},
	onLoad() {
		// this.$refs.checkbox.set({
		// 	type:'radio',		// 类型：复选框
		// 	column:2,				// 分列：3
		// 	list:this.list	// 列表数据
		// });
		this.pickerIndex = 3;
		//this.loadData();
		this.loadProduct();
		this.loadRefundList()
	},
	onShow() {},
	mounted() {},
	methods: {
		numberFilter(number) {
			return numberFormat(number);
		},
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handleNavbarClickLeft() {
			uni.switchTab({
				url: '/pages/my/my'
			});
		},
		unpayment(orderid){
			this.nowId = orderid
		                this.$refs.action.actionConfig={
		                    title:'请选择退款原因',
		                    summary:'',
		                    list:this.refundList,
		                    type:0,
		                    isBorderColor:false,
		                    specClass: 'show',
		                }
		            },
		  itemClick(index,type){
		                //这里根据不同的类型点击的索引值,做对应的逻辑处理
		                console.log(`你点击的action-sheet的类型是${type},list对应的索引值是${index}`)
						console.log(this.refundList[index].dictionaryid)
						if(index>-1){
							const senddata = {'orderid':this.nowId ,'reasoncode':this.refundList[index].name}
							this.$refs.loading.open();
							tokenpost(api.RefundApply, senddata)
								.then(res => {
									this.$refs.loading.close();
									if (res.status == 200 && res.data.returnCode == '0000') {
										this.$api.msg("退款操作成功！");
										this.pageIndex = 0;
										this.dataList = [];
										this.loadData();
									} else {
										this.$api.msg(res.data.returnMessage);
									}
								})
								.catch(error => {
									this.loadmore = 'more', 
									this.$refs.loading.close();
									this.$api.msg('请求失败fail');
								});
						}
		            },			
		tabChange(val) {
			console.log(val);
			switch (val) {
				case 0:
					this.content_show_id = 0;
					// this.$refs.checkbox.set({
					// 	type:'radio',		// 类型：复选框
					// 	column:2,				// 分列：3
					// 	list:this.list	// 列表数据
					// });
					this.pickerIndex = 3;
					break;
				case 1:
					this.content_show_id = 1;
					this.pageIndex = 0;
					this.TabCur = 1;
					this.dataList = [];
					console.log('点击了');
					this.loadData();
					break;
				default:
					this.content_show_id = 0;
					break;
			}
		},
		handlePayment(){
			uni.login({
				success: e => {
					console.log('code:',e.code);
					}
			})
		},
		handleSubmit() {
			console.log('发起支付');
			uni.login({
				success: e => {
					let data = this.$refs.checkbox.get();
					if (!data) {
						this.$api.msg('请选择要购买的产品!');
						return;
					}
					this.$refs.loading.open();
					this.loading = true;
					const sendData = { productid: data.productid, code: e.code };
					tokenpost(api.Orderypay, sendData)
						.then(res => {
							this.loading = false;
							if (res.status == 200 && res.data.returnCode == '0000') {
								console.log('得到接口prepay_id', res.data.data);
								let paymentData = res.data.data;
								uni.requestPayment({
									// provider: 'wxpay',
									timeStamp: paymentData.timeStamp,
									nonceStr: paymentData.nonceStr,
									package: paymentData.package,
									signType: 'MD5',
									paySign: paymentData.paySign,
									success: res => {
										uni.showToast({
											title: '支付成功!'
										});
										this.tabChange(1);
										uni.$emit('changecompany', { msg: 'company变化了' });
									},
									fail: res => {
										uni.showModal({
											content: '支付失败,原因为: ' + res.errMsg,
											showCancel: false
										});
									},
									complete: () => {
										this.$refs.loading.close();
									}
								});
							} else {
								this.$refs.loading.close();
								this.$api.msg(res.data.returnMessage);
							}
						})
						.catch(error => {
							this.loading = false;
							this.$refs.loading.close();
							this.$api.msg('请求失败fail');
							return;
						});
				},
				fail: e => {
					this.$refs.loading.close();
					console.log('fail', e);
					this.loading = false;
					uni.showModal({
						content: '支付失败,原因为: ' + e.errMsg,
						showCancel: false
					});
				}
			});
		},
		loadProduct() {
			this.$refs.loading.open();
			const senddata = {
				pageIndex: 1,
				pageRows: -1,
				exchangeintegralbegin: 1
			};
			tokenpost(api.GetProductList, senddata)
				.then(res => {
					this.$refs.loading.close();
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.productList = res.data.data.resultList;
						console.log(this.productList);
					} else {
						this.$api.msg(res.data.returnMessage);
					}
				})
				.catch(error => {
					(this.loadmore = 'more'), this.$refs.loading.close();
					this.$api.msg('请求失败fail');
				});
		},
		loadData() {
			this.loadmore = 'loading';
			this.$refs.loading.open();
			const senddata = {
				pageIndex: this.pageIndex + 1,
				pageRows: this.pageRows,
				ordertype: this.ordertype
			};
			tokenpost(api.GetOrderList, senddata)
				.then(res => {
					this.$refs.loading.close();
					if (res.status == 200 && res.data.returnCode == '0000') {
						if (res.data.data.resultList.length === 0) {
							this.loadmore = 'noMore';
							return;
						} else {
							this.dataList = this.dataList.concat(res.data.data.resultList);
							this.pageIndex = this.pageIndex + 1;
							this.loadmore = 'more';
						}
					} else {
						(this.loadmore = 'more'), this.$api.msg(res.data.returnMessage);
					}
				})
				.catch(error => {
					(this.loadmore = 'more'), this.$refs.loading.close();
					this.$api.msg('请求失败fail');
				});
		},
		loadRefundList() {
			const senddata = {'parentid':1000};
			tokenpost(api.GetRefundList, senddata)
				.then(res => {
					this.$refs.loading.close();
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.refundList = res.data.data.resultList
					} 
				})
				.catch(error => {
					this.$api.msg('退款原因加载失败!');
				});
		}
	}
};
